.App {
  font-family: sans-serif;
  color: #333333;
}

.sortable {
  list-style-type: none;
  margin: 0 0 20px;
  padding: 0;
}

.sortable-element {
  font-family: sans-serif;
  display: inline-block;
  background: #006098;
  color: #ffffff;
  padding: 10px 15px;
  margin: 0 2px;
  border-radius: 10px;
  cursor: pointer;
}

.collapsible {
  padding: 2px;
  margin: 2px;
}

.collapsible__header {
  padding: 5px;
  cursor: pointer;
  font-weight: bold;
}

.collapsible__body {
  padding: 10px;
}

.level-0 {
  background: hsl(50, 100%, 50%);
  border: 1px solid hsl(50, 100%, 45%);
}

.level-1 {
  background: hsl(50, 100%, 70%);
  border: 1px solid hsl(50, 100%, 45%);
}

.level-2 {
  background: hsl(50, 100%, 80%);
  border: 1px solid hsl(50, 100%, 45%);
}

.level-3 {
  background: hsl(50, 100%, 85%);
  border: 1px solid hsl(50, 100%, 45%);
}

.level-4 {
background: hsl(50, 100%, 90%);
  border: 1px solid hsl(50, 100%, 45%);
}

